}

.intro-section {
  background: var(--background-500-color);
  text-align: center;
  padding: 100px 0 200px;
}

.section-title {
  font-size: 32px;
  color: #ffffff;
}

.react-logo {
  margin: 30px auto 0;
  display: inline-block;
  width: 80px;
  height: 80px;
  position: relative;
  -webkit-animation: meat-spin 10s infinite linear;
          animation: meat-spin 10s infinite linear;
}

.react-logo__ball {
  width: 14px;
  height: 14px;
  background: var(--primary-color);
  border-radius: 50%;
  left: 50%;
  top: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.react-logo__oval {
  width: 80px;
  height: 30px;
  border-radius: 50%;
  background: var(--primary-color);
  opacity: 0.3;
  left: 50%;
  top: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.react-logo__oval--second {
  -webkit-transform: rotate(60deg);
      -ms-transform: rotate(60deg);
          transform: rotate(60deg);
  left: 0;
  top: 25px;
}

.react-logo__oval--third {
  -webkit-transform: rotate(-60deg);
      -ms-transform: rotate(-60deg);
          transform: rotate(-60deg);
  left: 0;
  top: 25px;
}

@-webkit-keyframes meat-spin {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(359deg);transform: rotate(359deg);}
}

@keyframes meat-spin {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(359deg);transform: rotate(359deg);}
}

.showcase-section {
  --skew-deg: 5deg;
  background: -webkit-gradient(linear, left top, left bottom, from(var(--background-400-color)), to(var(--background-500-color)));
  background: -webkit-linear-gradient(var(--background-400-color), var(--background-500-color));
  background: -o-linear-gradient(var(--background-400-color), var(--background-500-color));
  background: linear-gradient(var(--background-400-color), var(--background-500-color));
  padding-top: 100px;
  -webkit-transform: skew(0deg, calc(-1 * var(--skew-deg)));
      -ms-transform: skew(0deg, calc(-1 * var(--skew-deg)));
          transform: skew(0deg, calc(-1 * var(--skew-deg)));
  margin-top: -100px;
}

.showcase-section__content {
  -webkit-transform: skew(0deg, var(--skew-deg));
      -ms-transform: skew(0deg, var(--skew-deg));
          transform: skew(0deg, var(--skew-deg));
}

.case {
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 200px;
}

.case__images {
  position: relative;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -webkit-animation: float 6s ease-in-out infinite;
          animation: float 6s ease-in-out infinite;
}

.case__image {
  width: 200px;
  position: absolute;
  left: 0;
  -webkit-transform: rotate(-30deg);
      -ms-transform: rotate(-30deg);
          transform: rotate(-30deg);
  -webkit-transition: all 400ms ease-out;
  -o-transition: all 400ms ease-out;
  transition: all 400ms ease-out;
}

.case__normal-image {
  max-width: 100%;
}

.case__image:hover {
  cursor: pointer;
}

.case__image--active {
  -webkit-transform: scale(1.3) rotate(0deg);
      -ms-transform: scale(1.3) rotate(0deg);
          transform: scale(1.3) rotate(0deg);
  left: 40px;
}

.case__image--hidden {
  opacity: 0;
  pointer-events: none;
  -webkit-transform: translateY(50px) rotate(-30deg);
      -ms-transform: translateY(50px) rotate(-30deg);
          transform: translateY(50px) rotate(-30deg);
}

.case__image-active {
  left: 50%;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}

.case__details {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

.case__details--left {
  padding-right: 40px;
}

.case__title {
  font-size: 28px;
  margin-bottom: 0;
}

.case__features {
  padding-left: 25px;
}

@-webkit-keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-10px);
		        transform: translatey(-10px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-10px);
		        transform: translatey(-10px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

.contact-section {
  padding: 100px 0;
}

.big-container {
  margin: 0 auto;
  max-width: 1300px;
  padding: 0 15px;
}

.contact-container {
  display: -ms-flexbox;
  display: flex;
}

.my-picture-container {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  position: relative;
  margin: 0;
}

.my-picture {
  max-width: 100%;
}

.contacts {
  -ms-flex: 2 1 0%;
      flex: 2 1 0%;
  padding-left: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  background-image: url("./helsinki-map.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}

.marker {
  --marker-size: 70px;
  width: var(--marker-size);
  height: var(--marker-size);
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.map-ball {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  --size: 10px;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  background: var(--primary-color);
  z-index: 1;
}

.pin-effect {
  width: var(--marker-size);
  height: var(--marker-size);
  position: absolute;
  top: 0;
  display: block;
  background: rgba(21, 177, 238, 0.3);
  border-radius: 50%;
  opacity: 0;
  -webkit-animation: pulsate 2000ms ease-out infinite;
          animation: pulsate 2000ms ease-out infinite;
}

.pin-effect--delay {
  -webkit-animation: pulsate 2000ms ease-out 450ms infinite;
          animation: pulsate 2000ms ease-out 450ms infinite;
}

@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}

@keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}

.my-picture__caption {
  --from-edge: 30px;
  background: var(--primary-color);
  position: absolute;
  bottom: var(--from-edge);
  right: calc(-1 * var(--from-edge));
  left: var(--from-edge);
  padding: 20px;
}

.contact {
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-decoration: none;
  color: #ffffff;
}

.contact:hover::before {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.contact::before {
  margin-right: 10px;
  content: '{';
  -webkit-transform: translateX(20px);
      -ms-transform: translateX(20px);
          transform: translateX(20px);
  display: inline-block;
  opacity: 0;
  -webkit-transition: opacity 0.2s, -webkit-transform 0.3s;
  transition: opacity 0.2s, -webkit-transform 0.3s;
  -o-transition: transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s, -webkit-transform 0.3s;
}

.contact:hover::after {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.contact::after {
  margin-left: 10px;
  content: '}';
  -webkit-transform: translateX(-20px);
      -ms-transform: translateX(-20px);
          transform: translateX(-20px);
  display: inline-block;
  opacity: 0;
  -webkit-transition: opacity 0.2s, -webkit-transform 0.3s;
  transition: opacity 0.2s, -webkit-transform 0.3s;
  -o-transition: transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s, -webkit-transform 0.3s;
}

.contact__icon {
  font-size: 36px;
  width: 50px;
  text-align: center;
  margin-right: 10px;
  color: var(--primary-color);
}

.contact__title {
  font-size: 22px;
}

.section-title {
  text-align: center;
}


